"use client";

import { useEffect, useState } from "react";
import axios from "axios";
import Link from "next/link";
import { Button } from "@/components/ui/button";
import { Card } from "@/components/ui/card";
import { Input } from "@/components/ui/input";

interface CartItem {
  id: number;
  name: string;
  description: string;
  price: number;
  image_url: string;
  quantity: number;
}

export default function CartPage() {
  const [cartItems, setCartItems] = useState<CartItem[]>([]);
  const [loading, setLoading] = useState(true);
  const userId = 1; // Simulación: obtener userId de sesión o contexto

  useEffect(() => {
    const fetchCart = async () => {
      try {
        const response = await axios.get(`/api/cart?userId=${userId}`);
        setCartItems(response.data || []);
      } catch (error) {
        console.error("Error al cargar el carrito:", error);
      } finally {
        setLoading(false);
      }
    };
    fetchCart();
  }, [userId]);

  const updateCart = async (items: CartItem[]) => {
    try {
      await axios.post("/api/cart", { userId, items });
    } catch (error) {
      console.error("Error al guardar el carrito:", error);
    }
  };

  const handleQuantityChange = (id: number, quantity: number) => {
    if (quantity < 1) return;
    const updatedItems = cartItems.map(item =>
      item.id === id ? { ...item, quantity } : item
    );
    setCartItems(updatedItems);
    updateCart(updatedItems);
  };

  const handleRemove = (id: number) => {
    const updatedItems = cartItems.filter(item => item.id !== id);
    setCartItems(updatedItems);
    updateCart(updatedItems);
  };

  const totalPrice = cartItems.reduce(
    (total, item) => total + item.price * item.quantity,
    0
  );

  if (loading) {
    return (
      <div className="container my-5 text-center">
        <p>Cargando carrito...</p>
      </div>
    );
  }

  if (cartItems.length === 0) {
    return (
      <Card className="container my-5 p-8 text-center">
        <h3 className="text-2xl font-bold mb-4">Tu carrito está vacío</h3>
        <p className="text-gray-600 mb-6">
          Aún no has agregado juegos de mesa a tu carrito.
        </p>
        <Button asChild>
          <Link href="/catalogo">Ver Catálogo</Link>
        </Button>
      </Card>
    );
  }

  return (
    <div className="container mx-auto px-4 py-8">
      <Card className="p-6">
        <h2 className="text-2xl font-bold mb-6">Carrito de Compras</h2>
        <div className="space-y-4">
          {cartItems.map(item => (
            <div
              key={item.id}
              className="flex items-center space-x-4 p-4 border rounded-lg"
            >
              <img
                src={item.image_url}
                alt={item.name}
                className="w-24 h-24 object-cover rounded"
              />
              <div className="flex-grow">
                <h3 className="text-lg font-semibold">{item.name}</h3>
                <p className="text-gray-600">{item.description}</p>
                <p className="text-lg font-medium mt-2">
                  Precio: ${item.price.toFixed(2)}
                </p>
                <div className="flex items-center mt-2 space-x-4">
                  <label className="text-sm">Cantidad:</label>
                  <Input
                    type="number"
                    min={1}
                    value={item.quantity}
                    onChange={e =>
                      handleQuantityChange(item.id, parseInt(e.target.value))
                    }
                    className="w-20"
                  />
                  <Button
                    variant="destructive"
                    onClick={() => handleRemove(item.id)}
                  >
                    Eliminar
                  </Button>
                </div>
              </div>
            </div>
          ))}
        </div>
        <div className="mt-6 border-t pt-4">
          <h4 className="text-xl font-semibold">
            Total: ${totalPrice.toFixed(2)}
          </h4>
          <Button className="mt-4" size="lg">
            Proceder al Pago
          </Button>
        </div>
      </Card>
    </div>
  );
}
